
import { defineComponent, PropType } from "vue";
import { Article } from "@/util/news";

export default defineComponent({
  name: "HelloWorld",
  props: {
    article: {
      type: Object as PropType<Article>,
      required: true,
    },
  },
  setup(_) {
    const date = new Date(_.article.publishedAt).toLocaleString();
    return { date };
  },
});
